/* 남자는 css따위 쓰지 않는다 */
/* 나는 남자가 아니다 */

button {
  width: 200px;
  height: 50px;
  border-radius: 20px;
  margin:  30px;
  background-color: #252525;
  color: #D5E1E1;
  font-weight: 600;
  font-size: 16px;
}

.Container {
    display: grid;
    place-items: center; /* 가로 및 세로 중앙 정렬 */}

.ButtonCol{
  display: flex;
}

#opacitySection{
  display: grid;
  place-items: center; /* 가로 및 세로 중앙 정렬 */
}

@media (max-width: 768px) {
  button {
    width: 80%; /* 화면의 너비에 맞게 조정 */
    /* margin: 10px auto; 가운데 정렬 및 여백 조정 */
    height: auto;
    font-size: 80%;
  }

  .ButtonCol {
    flex-direction: column; /* 버튼들을 세로로 배치 */
    
  }
}